
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'


export default {
name: 'BetHeader',
components: {
XHeader,
Popup
},
props: ['method', 'curMethod', 'curSecond'],
computed: {
gameType() {
return this.$store.state.bet.gameType;
}
},
data() {
return {
show: false,
first: 'default'
};
},
methods: {
showPopup() {
if (this.curMethod != 'defalut') {
this.first = this.curMethod;
}
this.show = !this.show;
},
check(typeDetId) {
this.$emit('check', typeDetId);
this.show = false;
},

checkFirst(it) {
this.first = it.proType;
},

checkSecond(it) {
this.$emit('checkSecond', this.method[this.first], it);
this.show = false;
}
},
watch: {
curMethod() {
if (this.curMethod != 'defalut') {
this.first = this.curMethod;
}
}
}
};



